body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.collection.with-header {
   border: 0;
}

.center-align {
  padding: 32px 0;
  font-size: 32px;
  margin: 23px 0 10px 0;
}

.collection a.collection-item {
  color: #039be5;
}

.input-title {
  border: 1px solid gray !important;
}

label {
  font-size: 23px;
  color: rgb(95, 92, 92);
}


.save-btn {
  margin-left: 85%;
  margin-right: 4%;
  margin-top: 2%;
  padding: 5px;
}

.click-btn {
  margin-top: 2%;
  padding: 5px;
}

textarea {
  height: 180px;
}

.card.horizontal.row {
  margin: 0;
}

.card-image {
  margin-right: 137px;
}

.back-btn {
  margin-top: 23px;
  font-size: 23px;
}

.save-btn:disabled {
  background-color: red;
}

.footer {
  text-align: center;
  background-color: #e4f2fd;
  padding: 10px 0;
  margin-top: 23px;
}

.author-list {
  font-size: 20px;
}

.about-ourstory-headline {
  text-align: center;
  font-size: 23px;
  margin-top: 39px;
}

.all-posts {
  font-size: 28px;
}

.content-links {
  text-align: center;
}

.content-links h2 {
  font-size: 28px;
}

.content-links h4 {
  font-size: 28px;
  margin-top: 37px;
}




